import { $storeFlattened } from "./Storage.js";

const proxify = (proto, store) => {
  // for (const key in store) {
  const keys = Object.keys(store)
  for (let i = 0; i < keys.length; i++) {
    const key = keys[i]
      
    if (!ArrayBuffer.isView(store[key])) {
      proxify(proto[key], store[key])
    } else {
      const prop = store[key]
      // const get = function() {return prop[this._eid]}
      // const set = function(val) {prop[this._eid] = val}
      Object.defineProperty(proto, key, //{get,set})
        {
        get() {
          return prop[this._eid]
        },
        set(val) {
          prop[this._eid] = val
        }
      })
    }
  }
}

export const defineProxy = (store) => {

  function P (eid) {
    this._eid = eid
    this._store = store
  }

  // Object.keys(store).forEach((key,i) => {
  //   Object.defineProperty(P.prototype, key, {
  //     get() {
  //       const k = this._keys[i]
  //       return this._store[k][this._eid]
  //     },
  //     set(val) {
  //       const k = this._keys[i]
  //       this._store[k][this._eid] = val
  //     }
  //   })
  // })

  // P._store = store

  // P.prototype.proxify = function () {
  //   for (const key in P._store) {
  //     const prop = P._store[key]
  //     Object.defineProperty(proto, key, {
  //       get() {
  //         return prop[this._eid]
  //       },
  //       set(val) {
  //         prop[this._eid] = val
  //       }
  //     });
  //   }
  // }

  // P.prototype.proxify()

  proxify(P.prototype, store)

  // WARNING: cursed code ahead

  // this eval is to bypass v8 fucking over the performance when referencing things outside of getter/setter scope
  // don't blame me

  const defineProp = key => `
    Object.defineProperty(P.prototype, '${key}', {
      enumerable: true,
      get() {
        return this._store['${key}'][this._eid]
      },
      set(val) {
        this._store['${key}'][this._eid] = val
      }
    })
  `

  const defineAllProps = keys => keys.reduce((a,key) => a + defineProp(key), '')
  
  // eval(defineAllProps(Object.keys(store)))

  return Object.seal(P)
}

export const loadProxy = (proxy, eid) => {
  // proxy._eid = eid
  return proxy
}

export const defineProxyLoader = (components) => {
  const proxies = components.map((component) => new (defineProxy(component)))
  // return (eid) => proxies.map(proxy => loadProxy(proxy, eid))
  const fn = proxies.reduce((lastFn,proxy) => {
    return eid => {
      loadProxy(proxy, eid)
      lastFn(eid)
      return proxies
    }
  }, () => proxies)
  return fn
}